
.consent {
  position: fixed;
  top: 0;
  left: 50%;
  -webkit-transform: translate(-50%);
      -ms-transform: translate(-50%);
          transform: translate(-50%);
  padding: 10px;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  background: transparent;
}

.consent.collapsed {
  right: 3px;
  bottom: 4px;
  top: unset;
  left: unset;
  -webkit-transform: none;
      -ms-transform: none;
          transform: none;
  background: #151515;
}

.consent.collapsed::before {
  content: "^";
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translate(-50%);
      -ms-transform: translate(-50%);
          transform: translate(-50%);
}



