

.modals {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.modals .backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,.8);
}

.modals .modal {
  position: fixed;
  top: 10%;
  left: 10%;
  right: 10%;
  bottom: 10%;
}

.preview-modal {
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  text-align: center;
}

.preview-modal .spinner {
  position: absolute;
  left: 50%;
  top:50%;
  pointer-events: none;
}
.preview-modal .spinner::before {
  content: "â†»";
  font-size: 10vw;
  display: inline-block;
  -webkit-animation: spin-shifted 3s infinite;
          animation: spin-shifted 3s infinite;
}

@-webkit-keyframes spin-shifted {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0);
            transform: translate(-50%, -50%) rotate(0);
  }
  to {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
            transform: translate(-50%, -50%) rotate(360deg);
  }
}

@keyframes spin-shifted {
  0% {
    -webkit-transform: translate(-50%, -50%) rotate(0);
            transform: translate(-50%, -50%) rotate(0);
  }
  to {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
            transform: translate(-50%, -50%) rotate(360deg);
  }
}

.preview-modal h1 {
  position: absolute;
  top: -1.5em;
  width: 100%;
  pointer-events: none;
}

.preview-modal img {
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  left: 50%;
  top:50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.preview-modal .download {
  position: absolute;
  bottom: -1em;
  left: 50%;
  -webkit-transform: translate(-50%);
      -ms-transform: translate(-50%);
          transform: translate(-50%);
}
.preview-modal .download::before {
  content: ">> ";
}
.preview-modal .download::after {
  content: " <<";
}

.preview-modal .left,
.preview-modal .right {
  position: fixed;
  top: 10%;
  bottom: 10%;
  width: 7%;
  background-color: rgba(255,255,255,.3);
  cursor: pointer;
}
.preview-modal .left::before,
.preview-modal .right::before {
  top: 50%;
  left: 50%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 5vw;
}

.preview-modal .left {
  left: 0;
}
.preview-modal .left::before {
  content: "<";
}

.preview-modal .right {
  right: 0;
}
.preview-modal .right::before {
  content: ">";
}



