.highlight {
    color: #d0d0d0
}

.highlight table td {
    padding: 5px
}

.highlight table pre {
    margin: 0
}

.highlight .w {
    color: #d0d0d0
}

.highlight .err {
    color: #151515;
    background-color: #ac4142
}

.highlight .c,.highlight .cd,.highlight .cm,.highlight .c1,.highlight .cs {
    color: #888
}

.highlight .cp {
    color: #f4bf75
}

.highlight .o,.highlight .ow {
    color: #f4bf75
}

.highlight .p,.highlight .pi {
    color: #d0d0d0
}

.highlight .gi {
    color: #90a959
}

.highlight .gd {
    color: #ac4142
}

.highlight .gh {
    color: #6a9fb5;
    font-weight: bold
}

.highlight .k,.highlight .kn,.highlight .kp,.highlight .kr,.highlight .kv {
    color: #aa759f
}

.highlight .kc,.highlight .kt,.highlight .kd {
    color: #d28445
}

.highlight .s,.highlight .sb,.highlight .sc,.highlight .sd,.highlight .s2,.highlight .sh,.highlight .sx,.highlight .s1 {
    color: #90a959
}

.highlight .sr {
    color: #75b5aa
}

.highlight .si,.highlight .se {
    color: #8f5536
}

.highlight .nt,.highlight .nn,.highlight .nc,.highlight .no {
    color: #f4bf75
}

.highlight .na {
    color: #6a9fb5
}

.highlight .m,.highlight .mf,.highlight .mh,.highlight .mi,.highlight .il,.highlight .mo,.highlight .mb,.highlight .mx {
    color: #90a959
}

.highlight .ss {
    color: #90a959
}

body {
    margin: 0;
    padding: 0;
    background: #151515 url("bkg.jpg")!important;
    background-attachment: fixed!important;
    background-size: cover!important;
    color: rgba(0,172,234,1);
    font-size: 16px;
    line-height: 1.5;
    font-family: Monaco, "Bitstream Vera Sans Mono", "Lucida Console", Terminal, monospace
}

.container {
    width: 90%;
    max-width: 1000px;
    margin: 0 auto
}

section {
    display: block;
    margin: 0 0 20px 0
}

h1,h2,h3,h4,h5,h6 {
    margin: 0 0 20px
}

li {
    line-height: 1.4
}

header {
    background: rgba(0,0,0,0.1);
    width: 100%;
    border-bottom: 1px dashed rgba(249,189,5, 1);
    padding: 20px 0;
    margin: 0 0 40px 0
}

header h1 {
    font-size: 30px;
    line-height: 1.5;
    margin: 0 0 0 -40px;
    font-weight: bold;
    font-family: Monaco, "Bitstream Vera Sans Mono", "Lucida Console", Terminal, monospace;
    color: #f9bd05;
    text-shadow: 0 1px 1px rgba(0,0,0,0.1),0 0 5px rgba(181,232,83,0.1),0 0 10px rgba(181,232,83,0.1);
    letter-spacing: -1px;
    -webkit-font-smoothing: antialiased
}

header h1::before {
    content: "./ ";
    font-size: 24px
}

header h2,
footer h2 {
    font-size: 18px;
    font-weight: 300;
    color: rgba(0,172,234,.7)
}

footer {
    background: rgba(0,0,0,0.1);
    width: 100%;
    border-top: 1px dashed rgba(249,189,5, 1);
    padding: 20px 0;
    margin: 40px 0 0 0
}

footer h2 {
  margin: 0;
}

footer span + span {
  float: right;
}


#downloads .btn {
    display: inline-block;
    text-align: center;
    margin: 0
}

#main_content {
    width: 100%;
    -webkit-font-smoothing: antialiased
}

section img {
    max-width: 100%
}

h1,h2,h3,h4,h5,h6 {
    font-weight: normal;
    font-family: Monaco, "Bitstream Vera Sans Mono", "Lucida Console", Terminal, monospace;
    color: rgba(249,189,5, 1);
    letter-spacing: -0.03em;
    text-shadow: 0 1px 1px rgba(0,0,0,0.1),0 0 5px rgba(181,232,83,0.1),0 0 10px rgba(181,232,83,0.1)
}

#main_content h1 {
    font-size: 30px
}

#main_content h2 {
    font-size: 24px
}

#main_content h3 {
    font-size: 18px
}

#main_content h4 {
    font-size: 14px
}

#main_content h5 {
    font-size: 12px;
    text-transform: uppercase;
    margin: 0 0 5px 0
}

#main_content h6 {
    font-size: 12px;
    text-transform: uppercase;
    color: #999;
    margin: 0 0 5px 0
}

dt {
    font-style: italic;
    font-weight: bold
}

ul li {
    /* list-style-image: url("bullet.png") */
    list-style-type: none;
    position: relative;
}

ul li:before {
    content: "->";
    position: absolute;
    left: -2.1564em;
}

blockquote {
    color: #aaa;
    padding-left: 10px;
    border-left: 1px dotted #666
}

pre {
    background: rgba(0,0,0,0.9);
    border: 1px solid rgba(255,255,255,0.15);
    padding: 10px;
    font-size: 16px;
    color: rgba(249,189,5, 1);
    border-radius: 2px;
    word-wrap: normal;
    overflow: auto;
    overflow-y: hidden
}

code.highlighter-rouge {
    background: rgba(0,0,0,0.9);
    border: 1px solid rgba(255,255,255,0.15);
    padding: 0px 3px;
    margin: 0px -3px;
    color: #aa759f;
    border-radius: 2px
}

table {
    width: 100%;
    margin: 0 0 20px 0
}

th {
    text-align: left;
    border-bottom: 1px dashed rgba(249,189,5, 1);
    padding: 5px 10px
}

td {
    padding: 5px 10px
}

hr {
    height: 0;
    border: 0;
    border-bottom: 1px dashed rgba(249,189,5, 1);
    color: rgba(249,189,5, 1)
}

.btn {
    display: inline-block;
    background: -webkit-linear-gradient(top, rgba(0,172,234,0.3), rgba(0,172,234,0.3) 50%, rgba(0,172,234,0.3) 50%, rgba(0,172,234.3));
    padding: 8px 18px;
    border-radius: 50px;
    border: 2px solid rgba(0,0,0,0.7);
    border-bottom: 2px solid rgba(0,0,0,0.7);
    border-top: 2px solid #000;
    color: rgba(249,189,5, 0.8);
    font-family: Helvetica, Arial, sans-serif;
    font-weight: bold;
    font-size: 13px;
    text-decoration: none;
    text-shadow: 0 -1px 0 rgba(0,0,0,0.75);
    -webkit-box-shadow: inset 0 1px 0 rgba(255,255,255,0.05);
            box-shadow: inset 0 1px 0 rgba(255,255,255,0.05)
}

.btn:hover {
    background: -webkit-linear-gradient(top, rgba(249,189,5,0.3), rgba(249,189,5,0.3) 50%, rgba(249,189,5,0.3) 50%, rgba(249,189,5.3));
    color: rgba(0,172,234, 0.8);
}

.btn .icon {
    display: inline-block;
    width: 16px;
    height: 16px;
    margin: 1px 8px 0 0;
    float: left
}

.btn-github .icon {
    opacity: 0.6;
    background: url("blacktocat.png") 0 0 no-repeat
}

a {
    color: rgba(249,189,5, 1);
    text-shadow: 0 0 5px rgba(104,182,255,0.5);
}

a:not(.btn):hover {
    -webkit-filter: brightness(1.5);
            filter: brightness(1.5);
}

.cf::before,
.cf::after {
    content: "";
    display: table
}

.cf:after {
    clear: both
}

.cf {
    zoom:1
}

::-moz-selection{
    background:rgba(255, 153, 0,.3);
}

::selection{
    background:rgba(255, 153, 0,.3);
}

::-webkit-scrollbar-track-piece {
    border: transparent;
}
::-webkit-scrollbar-thumb:not(:hover) {
    border: 1px solid rgba(0,172,234,1);
}
::-webkit-scrollbar-thumb {
    border: 1px solid rgba(249,189,5,1);
}
::-webkit-scrollbar {
    max-width: 20px;
}



